.add_project {
    background-color: aliceblue;
    color: black;
    padding: 50px 20px 20px;
    width: 100%;
}

.add_project > button.close {
    height: 50px;
    width: 50px;
    border-radius: 25px;
    font-weight: 600;
    -webkit-box-shadow: 0 0 4px 0px grey;
            box-shadow: 0 0 4px 0px grey;
}

.add_project > form {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-align: center;
        align-items: center;
}

.add_project h2 {
    text-align: center;
}

.add_project input[type="text"],
.add_project textarea {
    margin-top: 20px;
    font-size: 20px;
}

.add_project input[type="text"] {
    height: 40px;
    width: 90%;
    border-radius: 4px;
}

.add_project input[type="checkbox"] {
    height: 40px;
    -ms-flex-item-align: start;
        align-self: flex-start;
    margin-left: 20px;
}

.add_project textarea {
    resize: none;
    width: 90%;
    height: 100px;
}


