.App {
  width: 100%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
}

.App > *:last-child {
  display: -ms-flexbox;
  display: flex;
  -ms-flex: 2 1;
      flex: 2 1;
}

.App > *:nth-child(2) {
  display: -ms-flexbox;
  display: flex;
  -ms-flex: 1.4 1;
      flex: 1.4 1;
}

.App > *:first-child {
  display: -ms-flexbox;
  display: flex;
  -ms-flex: 1 1;
      flex: 1 1;
}

.App > .projectBar {
  color: #000;
  background-color: transparent;
}

.App > .projectItems {
  color: #fff;
  background-color: #009CD7;
}

.App > .projectDetail {
  color: #000;
  background-color: transparent;
}



